import produce from "immer"
import * as types from "@/redux/mutation-types"

const globalState = {
	token: "",
	userInfo: "",
	assemblySize: "middle",
	language: "",
	themeConfig: {
		//  primary 
		primary: "#1890ff",
		// 
		isDark: false,
		// (weak) || (gray)
		weakOrGray: "",
		// 
		breadcrumb: true,
		// 
		tabs: true,
		// 
		footer: true
	}
}

// global reducer
const global = (state = globalState, action) =>
	produce(state, draftState => {
		switch (action.type) {
			case types.SET_TOKEN:
				draftState.token = action.token
				break
			case types.SET_ASSEMBLY_SIZE:
				draftState.assemblySize = action.assemblySize
				break
			case types.SET_LANGUAGE:
				draftState.language = action.language
				break
			case types.SET_THEME_CONFIG:
				draftState.themeConfig = action.themeConfig
				break
			default:
				return draftState
		}
	})

export default global
