import { message } from "antd";

/**
 * @description: 
 * @param {Number} status
 * @return void
 */
export const checkStatus = (status) => {
	switch (status) {
		case 400:
			message.error("")
			break
		case 401:
			message.error("")
			break
		case 403:
			message.error("")
			break
		case 404:
			message.error("")
			break
		case 405:
			message.error("")
			break
		case 408:
			message.error("")
			break
		case 500:
			message.error("")
			break
		case 502:
			message.error("")
			break
		case 503:
			message.error("")
			break
		case 504:
			message.error("")
			break
		default:
			message.error("")
	}
}
