import NProgress from "@/config/nprogress"
import axios from "axios"
import { showFullScreenLoading, tryHideFullScreenLoading } from "@/config/serviceLoading"
import { ResultEnum } from "@/enums/httpEnum"

import { checkStatus } from "./helper/checkStatus"
import { AxiosCanceler } from "./helper/axiosCancel"

import { setToken } from "@/redux/modules/global/action"
import { message } from "antd"
import { store } from "@/redux"

const axiosCanceler = new AxiosCanceler()

const config = {
	//  .env 
	// baseURL: '/api',
	baseURL: 'https://mock.mengxuegu.com/mock/62abda3212c1416424630a45',
	// 10s
	timeout: 10000,
	// 
	withCredentials: true
};

class RequestHttp {
	constructor(config) {
		// axios
		this.service = axios.create(config)

		/**
		 * @description 
		 *  -> [] -> 
		 * token(JWT) : token,redux/
		 */
		this.service.interceptors.request.use(
			(config) => {
				NProgress.start()
				// *  pending 
				axiosCanceler.addPending(config)
				// *  loading,api: { headers: { noLoading: true } }loadingloginApi
				config.headers.noLoading || showFullScreenLoading()
				const token = store.getState().global.token
				return { ...config, headers: { ...config.headers, "x-access-token": token } }
			},
			(error) => {
				return Promise.reject(error)
			}
		)

		/**
		 * @description 
		 *   -> [] -> JS
		 */
		this.service.interceptors.response.use(
			(response) => {
				const { data, config } = response
				NProgress.done()
				// * (loading)
				axiosCanceler.removePending(config)
				tryHideFullScreenLoading()
				// * code == 599
				if (data.code == ResultEnum.OVERDUE) {
					store.dispatch(setToken(""))
					message.error(data.msg)
					window.location.hash = "/login"
					return Promise.reject(data)
				}
				// * code
				if (data.code && data.code !== ResultEnum.SUCCESS) {
					message.error(data.msg)
					return Promise.reject(data)
				}
				// * 
				return data
			},
			async (error) => {
				const { response } = error
				NProgress.done()
				tryHideFullScreenLoading()
				//  response
				if (error.message.indexOf("timeout") !== -1) message.error("")
				// 
				if (response) checkStatus(response.status)
				// () :
				if (!window.navigator.onLine) window.location.hash = "/500"
				return Promise.reject(error)
			}
		)
	}

	// * 
	get(url, params, _object = {}) {
		return this.service.get(url, { params, ..._object })
	}
	post(url, params, _object = {}) {
		return this.service.post(url, params, _object)
	}
	put(url, params, _object = {}) {
		return this.service.put(url, params, _object)
	}
	delete(url, params, _object = {}) {
		return this.service.delete(url, { params, ..._object })
	}
}

export default new RequestHttp(config)
